<template>
  <div class="home" style="display: flex; justify-content: center">
    <el-card class="box-card">
        <h3> {{ date }}</h3>
        <el-form>
            <el-form-item v-for="(question, index) in questions" :key="index">
                <div>{{ question.text }}</div>
                <el-input v-if="question.type=='name' || question.type=='music' || question.type=='any'" v-model="question.answer"></el-input>
                <el-radio-group v-if="question.type=='yes/no'" v-model="question.answer">
                    <el-radio-button label="Yes"></el-radio-button>
                    <el-radio-button label="No"></el-radio-button>
                </el-radio-group>
                <el-slider
                    v-if="question.type=='bar'"
                    v-model="question.answer"
                    :step="1"
                    :max="10"
                    show-stops>
                </el-slider>
            </el-form-item>
            <el-form-item>
                <div style="display:flex">
                    <el-input v-model="prevID" placeholder="Link entry with your most recent entry code">
                        <template slot="append">
                            <el-button type="primary" @click="onSubmit">Submit</el-button>
                        </template>
                    </el-input>
                </div>
            </el-form-item>
        </el-form>
    </el-card>
  </div>
</template>

<script>
import db from '../firebase/init.js';
import { doc, setDoc, getDoc } from "firebase/firestore"; 
import ShortUniqueId from 'short-unique-id';
export default {
  name: 'Home',
  data() {
		return {
            questions: [],
            prevID: "",
            currentID: "",
            date: ""
		}
	},
  components: {
    
  },
  mounted() {
    var today = new Date();
    var dd = String(today.getDate()).padStart(2, '0');
    var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
    var yyyy = today.getFullYear();

    today = mm + '-' + dd + '-' + yyyy;
    this.date = today
    this.getQuestions();
  },
  methods: {
    async getQuestions() {
        let document = await getDoc(doc(db, "questions", '11-28-2022'))
        if (!document.exists()) {
            document = await getDoc(doc(db, "questions", this.date))
            return
        }
        Object.keys(document.data()).forEach(key => this.questions.push(JSON.parse(document.data()[key])))
    },
    async onSubmit() {
        if (this.questions.some(question => !question.answer)) {
            this.$notify.error({
                title: 'Error',
                message: 'Please answer all questions!'
                });
            return
        }
        if (this.prevID != "") {
            let document = await getDoc(doc(db, "entries", this.prevID))
            if (!document.exists()) {
                console.log("doesn't exist")
                return
            } else {
                if (document.data.nextID) {
                    console.log("already has a next element")
                    return
                }
            }
        }
        const uid = new ShortUniqueId();
        let currentID = uid()
        await setDoc(doc(db, "entries", currentID), {
            answerArr: JSON.stringify(this.questions),
            dayID: this.date,
            prevID: this.prevID,
            nextID: "",
        }); 
        if (this.prevID != "") {
            await setDoc(doc(db, "entries", this.prevID), {
            nextID: currentID,
        }, { merge: true }); 
        }
        this.currentID = currentID
        this.$confirm('Success! The code for this entry is: ' + currentID)
          .then(_ => {
            done();
          })
          .catch(_ => {});
    },
  }
}
</script>
<style>
  .text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }

  .box-card {
    width: 70%;
    padding: 10px;
    margin: 20px;
  }
</style>
